<template>
  <div class="register">
    <van-nav-bar title="注册" left-arrow @click-left="goPage('Login')" />

    <van-form>
      <van-field
        v-model="userInfo.phone"
        type="text"
        label="手机号"
        placeholder="(请输入11位手机号)"
        colon
        label-width="1.5rem"
        maxlength="11"
      />
      <van-field
        v-model="userInfo.nickName"
        type="text"
        label="昵称"
        colon
        label-width="50px"
        placeholder="(请输入1-10位以上数字或字母)"
        maxlength="10"
      />
      <van-field
        v-model="code"
        center
        clearable
        label="验证码"
        colon
        label-width="2rem"
        label-align="right"
        placeholder="(请输入验证码)"
      >
        <template #button>
          <van-button @click="refreshCode">
            <Sidentify :identifyCode="identifyCode"></Sidentify>
          </van-button>
        </template>
      </van-field>

      <van-field
        v-model="userInfo.password"
        type="text"
        label="密码"
        colon
        label-width="50px"
        placeholder="(请输入6位以上数字或字母)"
        maxlength="16"
        :type="isEye ? 'text' : 'password'"
        :right-icon="isEye ? 'eye-o' : 'closed-eye'"
        @click-right-icon="PasswordEye"
      />
      <div class="agree">
        <van-checkbox v-model="checked" checked-color="#ff4f18" label-disabled shape="square">
          注册即视为同意
          <a href="javascript:;">服务协议</a>
          和
          <a href="javascript:;">隐私条款</a>
        </van-checkbox>
      </div>

      <div class="register-btn" v-if="userInfo.phone == '' || code == ''|| userInfo.nickName == '' || userInfo.password =='' ">
        <van-button
          block
          type="info"
          disabled
          @click="register"
          native-type="submit"
        >注 册</van-button>
      </div>

      <div class="register-btn" v-else>
        <van-button
          block
          type="info"
          @click="register"
          native-type="submit"
        >注 册</van-button>
      </div>
    </van-form>
  </div>
</template>

<script>
//导入验证表单模块
import validForm from "../assets/js/validForm";
//导入验证码模块
import Sidentify from "../components/Sidentify.vue";

export default {
  components: {
    Sidentify,
  },
  data() {
    return {
      checked: false,
      //用户注册信息
      userInfo: {
        phone: "",
        password: "",
      },
      //false: 闭合，true：睁开
      isEye: false,
      identifyCodes: "1234567890",
      identifyCode: "",
      code: "",
    };
  },

  methods: {
    //切换密码的格式
    PasswordEye() {
      this.isEye = !this.isEye;
    },

    //跳转页面
    goPage(name) {
      this.$router.push({ name });
    },
    //注册
    register() {
      //检验验证码是否正确
      if (this.code == "") {
        this.$toast("请输入验证码");
        return;
      } else if (this.identifyCode !== this.code) {
        this.code = "";
        this.refreshCode();
        this.$toast("请输入正确的验证码");
        return;
      } else {
        //同意服务
        if ((this.checked = this.checked)) {
          //进行验证表单
          if (validForm.validUserForm(this.userInfo)) {
            //复制对象
            let data = Object.assign({}, this.userInfo);

            //请求密钥
            data.appkey = "U2FsdGVkX19WSQ59Cg+Fj9jNZPxRC5y0xB1iV06BeNA=";

            //开启加载提示
            this.$toast.loading({
              message: "加载中...",
              forbidClick: true,

              //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
              duration: 0,
            });

            //发起注册请求
            this.axios({
              method: "POST",
              url: "http://www.kangliuyong.com:10002/register",

              //post传递参数需要写在data
              data,
            })
              .then((result) => {
                //关闭加载提示
                this.$toast.clear();

                if (result.data.code == 100) {
                  //跳转到登录页面
                  this.$router.push({ name: "Login" });
                } else {
                  this.$toast(result.data.msg);
                }
              })
              .catch((err) => {
                //关闭加载提示
                this.$toast.clear();
              });
          }
        } else {
          this.$toast("请同意 服务协议 和 隐私条款");
        }
      }
    },
    randomNum(min, max) {
      return Math.floor(Math.random() * (max - min) + min);
    },

    //验证码
    refreshCode() {
      this.identifyCode = "";
      this.makeCode(this.identifyCodes, 4);
    },
    makeCode(o, l) {
      for (let i = 0; i < l; i++) {
        this.identifyCode += this.identifyCodes[
          this.randomNum(0, this.identifyCodes.length)
        ];
      }
    },
  },
  created() {
    //初始化验证码
    this.refreshCode();
  },
  mounted() {
    this.identifyCode = "";
    this.makeCode(this.identifyCodes, 4);
  },
};
</script>

<style lang="less" scoped>
.register {
  /deep/ .van-nav-bar__title {
    font-size: 18px;
  }
  /deep/.van-nav-bar__left {
    /deep/.van-nav-bar__arrow {
      font-size: 20px;
      color: black;
    }
  }
  /deep/ .van-field__label {
    text-align: right;
    color: rgb(115, 115, 115);
  }
  .van-field {
    width: 90%;
    margin: 27px 5%;
    border: 1px solid rgb(166, 166, 166);
    border-radius: 3px;
  }
  .van-checkbox {
    height: 30px;
    a {
      color: #ff4f18;
      text-decoration: underline;
    }
  }
  .van-cell:nth-child(3) {
    padding: 0;
  }
  .van-button--block {
    font-size: 15px;
    height: 44px;
    border-radius: 1rem;
    background-image: linear-gradient(90deg, #f10000, #ff2000 73%, #ff4f18);
  }
  /deep/ .van-icon-success {
    font-size: 15px;
  }
  /deep/.van-field__label--right{
      padding: 0;
  }
  .agree {
    width: 90%;
    margin: 30px 5%;
    color: rgb(115, 115, 115);
  }
  .register-btn {
    padding: 5px 20px;
  }
  .van-button--normal {
    border: 0;
    padding: 0;
  }
  .van-button--info {
    border: 0;
  }
  /deep/ .van-icon-closed-eye {
    color: #ccc;
  }
  /deep/ .van-icon-eye-o {
    color: #ccc;
  }
  @media screen and (max-width: 330px) {
    /deep/.van-field__label--right {
      padding-right: 0;
    }
    /deep/.van-field__label {
      margin-right: 0;
    }
  }
}
</style>